$skin-name : "ithaca";



// Ithaca Colors
$gray-dark: #213138;
$blue: #003B71;
$blue-light: #1169BA;
$blue-bright: #266DEA;
$blue-lightest: #73BCFF;
$blue-lighter: #51ACFF;
$blue-bright-light: #00DEFF;
$blue-dark: #162C41;
$blue-gray: #3A5A79;
$gray: #738BA3;
$gray-light: #C3D1D7;
$gray-lightest: #F3F6F7;
$green-light: #52EABC;
$green: #34D5A4;
$gold: #FFBB00;
$yellow: #FFE770;
$brown: #D5AE91;
$gray-brown: #D9D7D2;
$brown-light: #E8E5D9;
$gray-lighter: #EEEEEE;
$white: #FFFFFF;
$red: #EF5350;
$blue-bright-light-trans: rgba(0, 222, 255, 0.2);
$gold-trans: rgba(255, 187, 0, 0.2);
$black: #000000;
$gray-flat: #9C9C9C;



// Primary Colors
$top-header-background: $blue;
$text-color: $gray-dark;
$breadcrumbs-hover-color: $blue-light;
$focus-outline-color: $blue-bright;
$background-color-secondary: $blue;


// Logo location
$logo: "https://becm.ithaca.edu/wf/images/logo.svg";
$logo-height: 72px * 0.5;
$logo-width: 432px * 0.5;

// Tool icons
$skin-with-icons: true;
$icon-size: 20px;


/* topNav Main Menu */
$header-size: 50px;


// Icon Overrides
$sakai-custom-icons : (
    sakai-message-bundle-manager : ( extend : fa-edit ),
    sakai-blogwow : ( extend: fa-keyboard-o ),
    sakai-help : ( extend: fa-info-circle ),
    sakai-piazza : (extend : fa-comments-o),
    sakai-playposit : (extend : fa-lightbulb-o),
    sakai-qna : (extend : fa-question-circle-o),
    sakai-simple-rss : (extend : fa-rss),
    sakai-voicethread : (extend : fa-microphone),
    sakai-warpwire : (extend : fa-play-circle),
    sakai-attendance : (extend : fa-id-card-o)

);

// Tool Menu overrides

$tool-menu-item-hover-background-color: $white;
$tool-menu-item-hover-icon-color: $blue-gray;
$tool-menu-item-selected-text-color: $blue-light;
$tool-menu-item-selected-icon-color: $blue-light;
$tool-menu-item-selected-border-left: $gold;
$tool-menu-item-hover-border-left: $gold;
$all-sites-tool-menu-hover-left-border: $gold;
$tool-menu-item-selected-hover-border-left: $blue-light;


//MJ overrides
$sites-nav-menu-item-background: $white;
$sites-nav-menu-item-selected-color: $white;
$sites-nav-menu-item-selected-icon-color: $white;
$sites-nav-menu-item-selected-background: $white;
$sites-nav-menu-item-selected-border-color: $blue;
$sites-nav-menu-item-icon-color: $blue-light;
$sites-nav-submenu-item-icon-color: $blue-light;
$sites-nav-menu-item-hover-background: $white;
$sites-nav-menu-item-selected-hover-border-color: $blue;
$sites-nav-menu-item-hover-border-color: $blue-light;
$sites-nav-submenu-item-hover-left-border-color: $blue-light;
$all-sites-button-selected-border-color: $blue-light;
$all-sites-button-hover-border-color: $blue-light;
$sites-nav-menu-item-border-top: 4px solid $gold;
